import publishClose from '@/static/mbcImg/home/extendManageList/publishClose.png';
import publishGongYing from '@/static/mbcImg/home/extendManageList/publishGongYing.png';
import publishQiuGou from '@/static/mbcImg/home/extendManageList/publishQiuGou.png';
import { mapMutations, mapGetters } from 'vuex';
export default {
	name: 'gadget',
	data() {
		return {
			publishClose: publishClose,
			publishGongYing: publishGongYing,
			publishQiuGou: publishQiuGou
		};
	},
	created() {},
	mounted() {},
	computed: {
		i18n () {
		  return this.$t('publish')  
		}
	},
	methods: {
		...mapMutations({
			setPublishShow: 'setPublishShow'
		}),
		goToPutInBusiness() {
			console.log('to投放商机');
			let lookUserId = null;
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				lookUserId = landRegistLG.user.id;
			} else {
				lookUserId = uni.getStorageSync('UUID');
			};
			let urlParams = {
				isFlow: 0, // 平台来源
				orderType: 0, // 订单类型
				sourceTemp: 0, // 说明书来源
				aiListTemp: 0, // 是否展示ai
				purcId:  null, // 说明书id
				lookUserId: lookUserId, // 浏览者
				creatorId: '', // 说明书创建者
				purcState: '' // 说明书状态
			};
			uni.navigateTo({
				url: '/modules/publishPurchase/publishPurchase?urlParams=' + this.urlCrypto(urlParams, 0)
			});
		},
		goToCreateBusiness () {
			console.log('to选择模版(1/4)');
			uni.navigateTo({
				url: '/modules/createBusiness/selectTemplate'
			});
		},
		goToDataReport() {
			console.log('to数据报表');
			this.$router.push({
				path: '/dataReport',
				query: {}
			});
		},
		publishCloseFn() {
			console.log('触发关闭发布组件');
			this.$store.commit('setPublishShow', false); // 关闭发布组件
		}
	}
};
